/* Molecules - Cookie notice */
$cookie-notice--background-color: $base2-light;
$cookie-notice--background: transparent;
$cookie-notice--border: false; //$border-thin--standard $black

$cookie-notice--padding-top: $space-5; //1.25em;
$cookie-notice--padding-right: $space-8; //2em;
$cookie-notice--padding-bottom: $space-5; //1.25em;
$cookie-notice--padding-left: $space-8; //2em;
$cookie-notice--padding-medium-top: $space-3; //0.625em;
$cookie-notice--padding-medium-bottom: $space-3; //0.625em;

$cookie-notice__description--margin-top: $space-4; //0.9375em;
$cookie-notice__description--margin-right: $space-0; //0;
$cookie-notice__description--margin-bottom: $space-4; //0.9375em;
$cookie-notice__description--margin-medium-top: $space-0; //0;
$cookie-notice__description--margin-medium-right: $space-10; //2.5em;
$cookie-notice__description--margin-medium-bottom: $space-0; //0;

$cookie-notice__icon: "\e81c";
$cookie-notice__icon--margin-medium: $space-5 $space-5 $space-5 $space-0; //1.25em 1.25em 1.25em 0;
$cookie-notice__icon--padding: $space-0; //0;
$cookie-notice__icon--color: $black;
$cookie-notice__icon--font-size: $p-small;
$cookie-notice__icon--font-size-medium: $h4-medium;
$cookie-notice__icon--font-size-large: $h2-large;
$cookie-notice__icon--font-weight: normal;

$cookie-notice__button--min-width: 13ex;
